import { mapGetters } from "vuex";
import { cryptoData, decryptoData } from "../secure";

export default {
    computed:{
        ...mapGetters(['getIsAuth','getUsers'])
    },
    created(){
      //  let tokenId = JSON.parse(localStorage.getItem('tokenId'))
       let tokenId = decryptoData(localStorage.getItem('tokenId'))
       tokenId = JSON.parse(tokenId)
        if(tokenId)
        {
        let userId = tokenId.id
            this.getUsers.filter(response=>{
                if(response.id == userId){    
                    this.$store.commit('setActiveUser',response)
                }
            })
        }
        if(localStorage.token){
            this.$store.dispatch('hasToken',true)
        }
    },

    mounted(){

        if(!this.getIsAuth){
                this.$router.push('/login')
        }
    }
}